}
#about{
 padding-top: 100px;
 padding-bottom: 80px;
 padding-left: 80px;
 align-items: center;
 height:fit-content;
 box-sizing: border-box;

}
.skillCard{
   height: 200px;
    width:1200px;
    border-color:  #111111;
    border-width: 1px;
    border-style: solid;
    border-radius: 20px;
    box-shadow: 10px 10px 5px rgba(112, 112, 112, 0.584);
    padding-top: 35px;
    margin-top: 15px;
    margin-left: 7%;
}
.skillCard .icon{
    margin-top: 10px;
    font-size: 70px;
    margin-right: 50px;
    margin-bottom: 30px;
    
}
.skillCard .icon:hover{
   font-size: 80px;
}
.skillCard h2{
    font-size: 15px;
    margin-bottom: 30px;
    font-weight: 200;
    
}

