*, ::before, ::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }
  
  body {
    background: #fff;
  }
  
  .tabsContainer {
    padding-top: 50px;
    width: 250px;
    
    margin: 100px auto 0;
    word-break: break-all;
    border: 1px solid rgba(0, 0, 0, 0.274);
    
    border-style: solid;
  }
  
  .tabsGroup {
    display: flex;
  }
  .tabs {
    padding: 15px;
    text-align: center;
    width: 100px;
    cursor: pointer;
    border-bottom: 1px solid rgba(0, 0, 0, 0.008);
    box-sizing: content-box;
    position: relative;
    outline: none;
    border-color: #bae9d9;
   

  }
  .tabs:not(:last-child){
    border-right: 1px solid rgba(0, 0, 0, 0.274);
  }
  
  .active-tabs  {
    background: rgba(137, 134, 133, 0.169);
    border-style: solid;
  }
  
  .active-tabs::before {
    content: "";
    display: block;
    position: absolute;
    bottom: -5px;
    left: 50%;
    transform: translateX(-50%);
    width: calc(100% + 2px);
    height: 5px;
    background: #bae9d9;
  }
  

  .content-tabs {
    flex-grow : 1;
   
  }
  .content {
    background-color:rgba(177, 244, 224, 0.19);
    padding: 20px;
    width: 350px;
    height: 350px;
    display: none;
    border-radius: 15px;
   

  }
  .content h2 {
    padding: 0px 0 5px 0px;
  }
  .content hr {
    width: 100px;
    height: 2px;
    background: #222;
    margin-bottom: 5px;
  }
  .content p {
    width: 100%;
    height: 100%;
  }
  .active-content {
    display: block;
  }

  #ericsson{
    width : 100px;
  }

 