.home_start{
    margin-top: 140px;
    width:100%;
    position: relative;
    font-weight: 400;
    height: 610px;
    box-sizing: border-box;
    display:block; 
    overflow-x: hidden;
    
}
.intro span{
    font-size: 170px;
    /*color: #ecf9f4; */
    color: #bae9d9;
    
}
.intro p{
    padding-top: 20px;
    font-size: 160px;
    float:left;
    text-align: left;
    line-height: 1.2;
    
}

#me{
    height: 550px;
    border-radius: 50%;
}
.home_start #cvButton{
    padding-bottom: 20px;
}
a{
    text-decoration: none;
    color: black;
    font-size: 20px;
    float: left;
}

@media screen and (max-width: 700px) {
    .intro p{
        font-size: 70px;   
    }
    .intro span{
        font-size: 80px;
    }
    #me{
        height: 400px;
    }
    .home_start #cvButton{
       display:none;
    }
    .intro{
        align-items: center;
    }
}
@media screen and (max-width: 1290px) {
    #me{
     margin-bottom: 300px;
    }
  }
  @media screen and (max-width: 400px) {

    #me{
     height:300px;
    }
  }
