*{
    margin: 0;
    padding: 0;
    list-style: none;
    text-decoration: none;
    font-family: 'Sofia Sans Semi Condensed', sans-serif;
    box-sizing: border-box;
    color: #010403;

    
}
header{
 background-color: white;
    overflow: hidden;
    padding: 20px 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    top: 0;
    z-index: 999;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 999px;
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.5);
    
    


    
}
header a{
    text-decoration: none;
    color: #010403 ;
    padding:12px;
}

.logo h1{
    color: #010403 ;
    font-family: 'Sofia Sans Semi Condensed', sans-serif;
    font-weight: 100;
    align-self: center;
    padding-left: 40px;
    display: flex;
    justify-content: space-between;
  
}
.logo h1 a{
    font-size: 35px;
    align-self: center;
}

.logo h1 span{
    font-weight: 800;
}

nav{
    height: 100%;
    

    
}
nav a {
  padding-top: 10px;
  display: inline-block;
  
}

nav a:hover{
    background-color:#ecf9f4;;
}
.menu-icon {
    color: #010403;
    cursor: pointer;
    display: none;
  }

@media screen and (max-width: 920px) {
    nav a {
        display: block;
    }
    nav{
      
    display: none;
    position: absolute;
    top: 3.7rem;
    left: 0;
    width: 100%;
    background-color: var(--bgcolor);
    text-align: center;
    }
    .nav-bar.responsive {
        display: block;
      }
      .menu-icon {
        display: block;
        z-index: 1;
      }
      .logo h1{
        font-size: 1.4rem;
      }
  }
  @media screen and (max-width: 570px) {
    .icons{
        display: none;
    }
    .logo h1{
        text-align: left;
    }
    header{
        background-color: #ecf9f4;
    }
  }



.icons a{
    font-size:25px;
    padding-left: 20px;
    color: #010403;
   
}
.icons a:hover{
    font-size: 30px;
}